import requestHttps from '../../../common/request.js'
export default {
	data() {
		return {
			serverImg:getApp().globalData.serverImg,
			tabnav:['本月预估','注册时间','今日预估'],
			curent:0,
			sortTab:['今日直邀','本月直邀','团长进度'],
			sort:10,
			moreshow:true,
			seclist:['全部','有效','无效'],
			scur:10,
			sectshow:true,
			treanshow:true,
			spread_url:'8596587421',
			page:1,
			fanslist:[],
			looklist:[],
			count:'',
			prolodding:2,
			flag:true, 
			liloading:true,
			medix:true,
			
			
		}
	},
	methods: {
		gosech:function(){
			uni.navigateTo({
				url:"../fanssearch/fanssearch"
			})
		},
		getfanlist:function(){
			var that=this
			requestHttps({
				url:'/app/user/fans',
				method:'POST',
				data:{
					page:that.page 
				},
				success:res=>{
					console.log(res.data) 
					
					var data=res.data
					var list=res.data.data.lists
					that.count=res.data.data.count 
					var lood=2
					if(data.code==1){
						if (list != '') {
						      lood = 1
						  } else {
						      lood = 3
						  }
						  if(list==''&& that.page>1){
							  lood = 1
						  }
						 that.prolodding=lood
						if(that.page==1){
							that.fanslist=list
						}else{
							that.fanslist=that.fanslist.concat(list)
						}
						if(list.length>=1){
							that.flag=true
							that.medix=true
							// that.prolodding=1
							that.liloading=true
						}else{
							that.liloading=true
							that.flag=false
							that.medix=false 
							// that.prolodding=3
						}
					}
				}
			})
		},
		// 复制
		paste:function(value){
			console.log(value)
			uni.setClipboardData({
				data: value
			});
		},
		// 关闭个人信息
		colsebox:function(){
			var that=this
			that.treanshow=true
		},
		// 查看个人信息
		showtrea:function(e){
			var that=this
			var index=e.currentTarget.dataset.index
			var looklist=that.fanslist[index]
			that.looklist=looklist
			that.treanshow=false
		},
		// 本月预估切换
		switnav:function(e){
			console.log(e)
			var that=this
			var cur=e.currentTarget.dataset.index
			that.curent=cur
			that.moreshow=true
			
		},
		// 更多筛选
		mownav:function(e){
			var that=this
			that.curent=10
			that.moreshow=!that.moreshow
		},
		// 筛选按钮下面的排序
		sottab:function(e){
			var that=this
			var cur=e.currentTarget.dataset.cur
			that.sort=cur
			that.curent=10
			that.moreshow=true
		},
		// 粉丝筛选
		sectnav:function(e){
			var that=this
			var scur=e.currentTarget.dataset.scur
			that.scur = scur
			that.sectshow=true
		},
		scrclick:function(){
			var that=this
			that.sectshow=!that.sectshow
		},
		// 邀请明细
		goinduct:function(){
			var that=this
			uni.navigateTo({
				url:'../fans_detail/fans_detail'
			})
		},
		// 返回上个页面
		goback:function(){
			uni.navigateBack({
				delta:1
			})
		},
		
	},
	onLoad:function(){
		var that=this
		that.getfanlist()
	},
	onReachBottom:function(){
		var that=this
		if(that.flag){
			var page=that.page
			page++
			that.page=page
			that.liloading=false
			that.getfanlist()
		}
	}
}
